import React, { useState } from "react";
import NContext from "./ContextC";

const NState = (props) => {
  const [num, setNum] = useState(0);
  // const [nameC, setNameC] = useState("/");
  let val = 0;
  const handleAdd = () => {
    val = num + 5;
    setNum(val);
  };
  const handleSub = () => {
    val = num - 1;
    setNum(val);
  };

  return (
    <NContext.Provider value={{ num, handleAdd, handleSub }}>
      {props.children}
    </NContext.Provider>
  );
};

export default NState;
